import React, { Component } from 'react'
import styled from 'styled-components'

export const FormContainer = styled.form``

const Input = styled.input`
  border-radius: 3px;
  border: 1px solid ${props => props.theme.titleTextColor};
  background-color: ${props => props.theme.titleTextColor};
  display: block;
  margin: 0 0 1em;
  padding: 0.6rem;
  font-size: 1rem;
  ::placeholder {
    color: ${props => props.theme.placeholderTextColor};
  }
`
const TextInput = styled(Input).attrs({
  type: 'text',
  size: props => (props.small ? 5 : undefined),
})``

const PasswordInput = styled(Input).attrs({
  // Every <PasswordInput /> should be type="password"
  type: 'password',
})``

export const ActionButton = styled.button`
  border-radius: 3px;
  display: block;
  margin: 0 0 1em;
  padding: 0.6rem;
  font-size: 1rem;
  color: ${props => props.theme.actionColor};
`
export default class RSVPForm extends Component {
  constructor(props) {
    super(props)
  }

  render() {
    const { onChange, handleSubmit, name, email, submitting } = this.props
    return (
      <FormContainer>
        <TextInput
          name="name"
          placeholder="Full Name"
          onChange={onChange}
          value={name}
          disabled={submitting}
        />
        <TextInput
          name="email"
          placeholder="you@email.com"
          value={email}
          onChange={onChange}
          disabled={submitting}
        />

        <ActionButton onClick={handleSubmit}>RSVP</ActionButton>
      </FormContainer>
    )
  }
}
